.history{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display : flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(128, 17, 17);
}

.match{
    background-color: rgb(35, 109, 131);
    transition: all 500ms ease-in;
    display : flex;
    flex-direction: row;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid  black;
}

.match2{
    background-color: rgb(35, 109, 131);
    transition: all 500ms cubic-bezier(0, 1.1, 1, 0.98);;
    display : flex;
    flex-direction: row;
    border-left: 1px solid  black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    flex : 60px;
}

.historychampimg{
    width: 20%;
    height: 20%;
    padding: 10px;
    border-radius: 200px;
}

.matchdate{
    opacity: 0;
    background-color: rgb(33, 150, 218);
    border: solid rgb(2, 87, 167) 2px;
    border-radius: 6px 6px 8px 8px;
    padding: 4px;
    z-index: 2;
    position: absolute;
    margin-top: -25px;
    margin-left:10px;
    transition: opacity cubic-bezier(0.53, 0.11, 0.49, 1.09) 200ms;
}

.matchinfos{
    flex-direction: column;
    display: flex;
    justify-content: space-evenly;
}

.matchago:hover > .matchdate{
    opacity: 1;
}